@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --text-b: Inter;
  --headline-extra-bold-h4: Roboto;
  --font-poppins: Poppins;

  /* font sizes */
  --text-b-size: 1rem;
  --font-size-21xl: 2.5rem;
  --button-btn-size: 0.88rem;
  --font-size-smi-8: 0.8rem;
  --font-size-11xl: 1.88rem;
  --font-size-xl: 1.25rem;
  --font-size-13xl: 2rem;
  --headline-extra-bold-h4-size: 1.75rem;
  --font-size-lg: 1.13rem;

  /* Colors */
  --white-background: #fff;
  --black-fonts-headings: #2d2e2e;
  --color-gray-100: #7d7d7d;
  --black: #262626;
  --color-gray-200: #1f2027;
  --black1: #000;
  --color-whitesmoke-100: #f3f3f3;
  --color-whitesmoke-200: #f2f2f2;
  --color-whitesmoke-300: #ebebeb;
  --muted-color: #78858f;
  --color-coral: #f27a44;
  --color-indianred: #be554b;
  --color-dimgray: #505c59;

  /* Gaps */
  --gap-base: 1rem;
  --gap-xs-7: 0.73rem;
  --gap-7xs-8: 0.36rem;
  --gap-41xl: 3.75rem;
  --gap-xl: 1.25rem;
  --gap-3xs: 0.63rem;
  --gap-9xs: 0.25rem;
  --gap-10xs: 0.19rem;
  --gap-21xl: 2.5rem;

  /* Paddings */
  --padding-base: 1rem;
  --padding-10xl-2: 1.83rem;
  --padding-base-2: 0.95rem;
  --padding-mid-5: 1.09rem;
  --padding-2xl: 1.31rem;
  --padding-3xs: 0.63rem;
  --padding-6xl: 1.56rem;
  --padding-19xl: 2.38rem;
  --padding-7xl: 1.63rem;
  --padding-xl: 1.25rem;
  --padding-31xl: 3.13rem;

  /* Border radiuses */
  --br-8xs-7: 4.7px;
  --br-xs-7: 11.7px;
  --br-11xl: 30px;
  --br-xl: 20px;
  --br-31xl: 50px;
  --br-2xl: 21px;
}
